<template>
	<div v-show="show" class="error_item_wrap">
		<transition
				name="custom-classes-transition"
				enter-active-class="animated flipInX"
				leave-active-class="animated fadeOut">
			<div v-show="show" :class="['error_item_wrap__item', className]">
				<span class="error_inner"><slot></slot></span>
				<i class="el-icon-caret-right icon_right"></i>
			</div>
		</transition>
	</div>
</template>
<script>
	export default {
		props: {
			show: {type: Boolean},
			text: {type: String},
			className: {type: String},
			leftMargin: {type: Number, default:() => {return 0}}
		},
		data() {
			return {}
		},
		methods: {},
		watch: {
			show(val){
				if(val === true){
					setTimeout(() => {
						let $item = $(this.$el).find('.error_item_wrap__item');
						$item.css({
							left: '-'+ _.toInteger($item.width() + 17 + this.leftMargin) + 'px'
						})
					}, 10)
				}
			}
		},
		components: {},
		computed: {},
		mounted(){
		
		}
	}
</script>
<style lang="scss">
	.error_item_wrap{
		&__item{
			position: absolute;
			padding: 6px 12px;
			background: #d1404a;
			color: #fff;
			border-radius: 4px;
			top: 10px;
			left: -35%;
			font-size: 0.8em;
			display: flex;
			justify-content: center;
			align-items: center;
			
			.icon_right {
				position: relative;
				right: -20px;
				color: #d1404a;
				font-size: 1em;
			}
		}
	}
</style>


