"use strict";
import Promise from 'promise/lib/es6-extensions';

export default class {

	constructor(props = {}) {
		let defaultProps = {
			mapId: 'map_order_hidden',
			mainComponent: '#main_order_form',
			mapOptions: {
				center: [53.195522, 50.101819],
				zoom: 9,
				behaviors: [],
				controls: [],
				suppressObsoleteBrowserNotifier: true
			},
			hiddenMap: true,
			coordUrl: '/rest2/public/order/coords',
			areaClickHandler: false
		};
		this.props = Object.assign({}, defaultProps, props);

		this.container = $(this.props.mainComponent);
		this.mapContainer = this.container.find(this.props.mapId);
		if (this.mapContainer.length === 0) {
			this.container.append(`<div id="${this.props.mapId}" ${this.props.hiddenMap === true ? 'style="display: none"' : false}></div>`);
			this.mapContainer = BX(this.props.mapId);
		}

		this.mapData = {};
		this.yandex = {};
		this.polygons = [];
		this.currentPointObject = {};
		this.currentPolygon = {};
	}

	_ready(){
		return new Promise((resolve, reject) => {
			try {
				ymaps.ready().then(res => {
					this.yandex = res;
					this.mapData = new this.yandex.Map(this.mapContainer, this.props.mapOptions);
					resolve(this);
				}).catch(err => {
					console.error(err);
				});

			}catch (err) {
				reject(err);
			}
		});
	}

	_loadCoords(){
		return new Promise((resolve, reject) => {
			Vue.http.get(this.props.coordUrl).then(coords => {
				if(coords.data.DATA !== null){
					_.forEach(coords.data.DATA.DATA, (value, i) => {
						let polyProp;
						if (!_.isEmpty(value.CORDS)) {
							polyProp = JSON.parse(value.CORDS);
						} else {
							polyProp = {
								cords: [[]],
								options: {}
							};
						}

						let UlPolygon = new this.yandex.Polygon(polyProp.cords, {}, polyProp.options);
						this.mapData.geoObjects.add(UlPolygon);
						this.polygons.push(UlPolygon);

						if(this.props.areaClickHandler instanceof Function){
							const _self = this;
							UlPolygon.events.add('click', function () {
								_self.props.areaClickHandler(UlPolygon, _self.yandex).then(res => {
									if(res.data.DATA.BACK_URL){
										window.location.assign(res.data.DATA.BACK_URL);
									}
								});
							})
						}
					});

					resolve(this);
				} else {
					reject(coords.data.ERROR_MSG);
				}
			});
		});

	}

	loadMap() {
		return this._ready().then((res) => {
			return this._loadCoords();
		});
	}

	search(address = '') {
		return new Promise((resolve, reject) => {
			if(!_.isEmpty(address)){

				let currentPolygon, polygons = this.polygons;
				let GeocodeStart = ymaps.geocode(address, {results: 1});


				GeocodeStart.then(res => {
					try {
						this.mapData.geoObjects.remove(this.currentPointObject);
						let searchInt = 0;
						for (let k in polygons) {
							let arPolygon = polygons[k];

							this.currentPointObject = res.geoObjects;

							let contains = this.yandex.geoQuery(res.geoObjects).searchIntersect(arPolygon);
							this.mapData.geoObjects.add(res.geoObjects);

							if (contains.getLength() === 1) {
								searchInt++;
								currentPolygon = arPolygon;
								break;
							}
						}

						if(searchInt > 0){
							this.currentPolygon = currentPolygon;
						} else {
							// throw new Error(false);
						}


						resolve(searchInt);

						// if (searchInt > 0) {
						// 	return true;
						// }
					} catch (err){
						reject(err);
					}
				});
			} else {
				reject(err);
			}
		});
	}
	async generatorSearch(listAddress){
		for(let value of listAddress) {
			value.inArea = await this.search(value.address);
		}
		return listAddress;
	}
}



