<template>
	<div :class="['modal_wrap', classWrap]">
		<transition name="custom-classes-transition"
				enter-active-class="animated fadeIn"
				leave-active-class="animated fadeOut">
			<div class="main_modal_overlay" v-if="show" @click.prevent="overlayClick"></div>
		</transition>
		<div :class="['modal_content', classContent]" v-show="show" @click.self="overlayClick"><slot></slot></div>
	</div>
</template>
<script>
	export default {
		props: {
			show: {type: Boolean, default: () => {return false}, required: false},
			classWrap: {type: String},
			classContent: {type: String},
			closeOverlay: {type: Boolean, default: ()=> {return true}}
		},
		data() {
			return {}
		},
		methods: {
			overlayClick(){
				if(this.closeOverlay === true)
					this.$emit('close-modal')
			},
		},
		watch: {
			show(val){
				let $wrap = $('body');
				if(val === true){
					setTimeout(() => {
//						console.info($wrap.height());
						$wrap.addClass('body_overflow');
					})
				} else {
					$wrap.removeClass('body_overflow');
				}
			},
		},
		created() {
		},
		beforeUpdate() {
		},
		components: {}
	}
</script>
<style lang="sass">
	.main_modal_overlay
		background: rgba(255,156,34,0.69)
		position: fixed
		height: 100%
		width: 100%
		opacity: 0.8
		z-index: 500
		top: 0
		left: 0
		overflow: hidden
	
	.body_overflow
		overflow: hidden
	
	.modal_content
		position: absolute
		top: 0
		left: 0
		max-width: 1600px
		min-width: 1200px
		height: 100vh
		width: 100%
		z-index: 501
		display: flex
		flex-wrap: nowrap
	
	
</style>


